// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import { sync } from 'vuex-router-sync'
import FastClick from 'fastclick'

import AlloyFinger from 'alloyfinger'
import AlloyFingerPlugin from 'alloyfinger/vue/alloy_finger_vue'

import App from './App'
import router from './router'
import store from './store'

import './assets/fonts/iconfont/iconfont.css'

//  Android 4.4.2  service worker 
//  Android 6  service worker 
//  service worker  service-worker.js  service worker 
// import registerServiceWorker from './registerServiceWorker'

import './i18n/vuex-i18n'
import './utils/directives'
import './utils/filters'
import './platform/hmap/env'
import { Indicator, Toast } from 'hips'

Vue.use(AlloyFingerPlugin, {
  AlloyFinger,
})
var eventBus = {
  install (Vue, options) {
    Vue.prototype.$bus = new Vue()
  },
}
Vue.use(eventBus)
Vue.use(Indicator)
Vue.use(Toast)

//  alpha: vconsole 
if (process.env.CONFIG_ENV === 'alpha') {
  const VConsole = require('vconsole')
  new VConsole() // eslint-disable-line
}

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
})

// 
var deviceIsWindowsPhone = navigator.userAgent.indexOf('Windows Phone') >= 0
var deviceIsIOS = /iP(ad|hone|od)/.test(navigator.userAgent) && !deviceIsWindowsPhone
FastClick.prototype.focus = function (targetElement) {
  var length
  // Issue #160: on iOS 7, some input elements (e.g. date datetime month) throw a vague TypeError on setSelectionRange. These elements don't have an integer value for the selectionStart and selectionEnd properties, but unfortunately that can't be used for detection because accessing the properties also throws a TypeError. Just check the type instead. Filed as Apple bug #15122724.
  if (deviceIsIOS && targetElement.setSelectionRange && targetElement.type.indexOf('date') !== 0 && targetElement.type !== 'time' && targetElement.type !== 'month') {
    length = targetElement.value.length
    targetElement.setSelectionRange(length, length)
    targetElement.focus()
  } else {
    targetElement.focus()
  }
}

FastClick.attach(document.body)
sync(store, router)
// registerServiceWorker()



