/**
 * 
 */

import http from '@/utils/http'

// 
function getPurchaseApprovalList ({commit}, data) {
  const url = `r/api?sysName=PoApprovalService&apiName=getPoApprovelList&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

//  {approveStatus: APPROVEDREJECTED}
function purchaseOperation ({commit}, data) {
  const url = `r/api?sysName=PoApprovalService&apiName=submitPoApprovel&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

export default {
  getPurchaseApprovalList,
  purchaseOperation,
}



