import http from '@/utils/http'

// 
function getCurrentWorkerInfoApi ({commit}, data) {
  const url = `r/api?sysName=moCommonService&apiName=getCurrentWorkerInfo&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

