import http from '@/utils/http'

// 
function getPickOrderList ({commit}, data) {
  const url = `r/api?sysName=pickReportService&apiName=queryPickReports&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

