import http from '@/utils/http'

// id
function getOrgList ({ commit }, data) {
  const url = `r/api?sysName=equipmentService&apiName=getOrganization&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}
