import http from '@/utils/http'

// 
function getReportListName ({commit}, data) {
  const url = `r/api?sysName=LovService&apiName=getSysCodeLovInfo&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}
