import http from '@/utils/http'

// 
function getWorkerApi ({commit}, data) {
  const url = `r/api?sysName=equipmentService&apiName=getWorker&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

