import http from '@/utils/http'

function queryDefaultApi ({commit}, data) {
  const url = `r/api?sysName=MainPageService&apiName=queryDefault&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

