/**
 *  main 
 */
function setData (state, data) {
  // console.info('data' + data)

  if (data.type === 'searchResult') {
    state.searchResult = data.result
  }
  if (data.type === 'tabtype') {
    state.myOrderTabType = data.result
  }
  if (data.type === 'commonItem') {
    state.commonItem = data.result
  }
}

export default {
  setData,
}



