import http from '@/utils/http'

// MO
function getMoListApi ({commit}, data) {
  const url = `r/api?sysName=moWorkOrderService&apiName=getMoList&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

