import http from '@/utils/http'

// 
function getEquipmentApi ({ commit }, data) {
  const url = `r/api?sysName=EquipmentService&apiName=getEquipment&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

